<template>
    <div class="d-flex p-0">
        <SideBar activeElement="dashboardLink"></SideBar>
        <div class="container">
            <div class="row row-cols-auto justify-content-around">
                <template v-for="item in widgetArray" :key="item.id">
                    <div class="col">
                        <genericWidget :widgetInfo="item"></genericWidget>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import SideBar from "@/components/SideBar/SideBar.vue";
import genericWidget from "@/components/dashboard/widgets/genericWidget.vue";
import axios from "axios";

export default {
    name: "Dashboard",
    data() {
        return {
            widgetArray: [],
        };
    },
    components: {
        SideBar,
        genericWidget
    },
    beforeMount() {
        axios({
            method: "post",
            // url: "http://localhost:8080/api/user/finduser",
            url: "https://www.emuix.com/api/user/finduser",
            data: {
                email: "nat@gmail.com"
            }
        },
        )
            .then((response) => {
                console.log("Then from res 200 : ", response.data);
                this.widgetArray = response.data.data;
            })
            .catch((response) => {
                console.log("Then from res error : ", response.response.data.message);
            });
    },
    methods: {
        btnAdd() {
            console.log("click");
        },
    },
};
</script>

<style scoped>
.block {
    border-radius: 12px;
    height: 250px;
    width: 250px;
    background-color: #145374;
    display: table-cell;
    vertical-align: middle;
    color: #ffffff;
}

</style>