<template>
    <div class="d-flex p-0">
        <SideBar activeElement="editDashboardLink"></SideBar>
        <div class="container">
            <div class="row top-buffer row-cols-auto" ref="infoBox">
                <template v-for="item in widgetArray" :key="item.id">
                    <div class="col">
                        <genericWidgetAdd :widgetInfo="item"></genericWidgetAdd>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import SideBar from "@/components/SideBar/SideBar.vue";
import genericWidgetAdd from "@/components/dashboard/widgetsAdd/genericWidgetAdd.vue";
import axios from "axios";


export default {
    name: "addWidget",
    data() {
        return {
            widgetArray: [],
            isSelected: false,
        };
    },
    components: {
        SideBar,
        genericWidgetAdd
    },
    beforeMount() {
        axios({
            method: "get",
            // url: "http://localhost:8080/api/widget/findall",
            url: "https://www.emuix.com/api/widget/findall",
        })
            .then((response) => {
                console.log("Then from res 200 : ", response.data);
                this.widgetArray = response.data;
            })
            .catch((response) => {
                console.log("Then from res : ", response.response.data.message);
            });
    },
    mounted() {
    },
    beforeDestroy() {
        window.removeEventListener("resize", this.myEventHandler);
    },
    methods: {
    },
};
</script>

<style scoped>
.top-buffer {
    margin-top: 20px;
    height: v-bind(widgetContainerHeigth+"px");
}
</style>