<template>
    <div @click="btnAdd" :class="isSelected ? 'block is-green' : 'block is-grey'">
        <div class="widget d-flex flex-column posSet">
            <strong class="title">{{ this.name }}</strong>
            <div>
                <WeatherWidgetAdd v-if="isWeatherWidget" />
                <HueWidgetAdd v-if="isHueWidget" />
            </div>
        </div>
    </div>
</template>

<script>
import WeatherWidgetAdd from "@/components/dashboard/widgetsAdd/specWidget/WeatherAdd.vue"
import HueWidgetAdd from "@/components/dashboard/widgetsAdd/specWidget/HueAdd.vue"

export default {
    name: "genericWidgetAdd",
    data() {
        return {
            id: null,
            name: null,
            bgColor: "#145374",

            isSelected: false,

            isWeatherWidget: false,
            isHueWidget: false,
        };
    },
    components: {
        WeatherWidgetAdd,
        HueWidgetAdd
    },
    props: {
        widgetInfo: {}
    },
    beforeMount() {
    },
    mounted() {
        this.name = this.widgetInfo.name;
        this.id = this.widgetInfo._id;
        // console.log("Widget : ", this.widgetInfo);

        switch (this.id) {
            case "629a5ed82f6d9bf0c71333f9": //weather
                this.isWeatherWidget = true;
                break;
            case "629a65232f6d9bf0c71333fa": //Hue
                this.isHueWidget = true;
                break;

            default:
                break;
        }
    },
    beforeDestroy() {
    },
    methods: {
        btnAdd() {
            this.isSelected = true;

            switch (this.id) {
                case "629a5ed82f6d9bf0c71333f9": //weather
                    this.$router.push({ name: 'WeatherConfig' })
                    break;
                case "629a65232f6d9bf0c71333fa": //Hue

                    break;

                default:
                    break;
            }
        },
    },
};
</script>

<style scoped>
.widget {
    border-radius: 12px;
    height: 200px;
    width: 200px;
    background-color: v-bind(bgColor);
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
}

.block {
    border-radius: 20px;
    border-width: 4px;
    border-style: solid;
    height: 215px;
    width: 215px;
    /* background-color: #50b8f0; */
    /* color: #ffffff; */
    display: flex;
    align-items: center;
    justify-content: center;
}

.is-grey {
    border-color: rgba(0, 73, 0, 0.356);
}

.is-green {
    border-color: rgb(22, 194, 22);
}

.title {
    position: absolute;
    top: 20px;
    left: 20px;

}
.posSet {
    position: relative;
}

.top-space {
    padding-top: 30px;
}
</style>