<script>
// import axios from 'axios';
import { weatherUpdate } from "@/assets/update";

export default {
    name: "WeatherWidget",
    data() {
        return {
            id: null,
            name: null,
            result: null,
            temp: null,
            humidity: null,
            description: null
        };
    },
    components: {
    },
    props: {
        widgetProps: {}
    },
    beforeMount() {
        this.result = weatherUpdate(this.widgetProps.parameter.location)
        this.result.then(
            (value) => {
                this.result = value;
            }
        );
    },
    mounted() {
        this.result.then(
            (value) => {
                this.temp = value.main.temp;
                this.humidity = value.main.humidity;
                this.description = value.weather[0].description;
            }
        );
    },
    beforeDestroy() {
    },
    methods: {
        btnTest() {
            console.log("reuslt : ", this.result);
            console.log("reuslt : ", this.temp);


            // this.result.then(
            //     (value) => {
            //         console.log(value);
            //     }
            // );
        }
    },
};
</script>

