<template>
    <div class="d-flex p-0" :key="componentKey">
        <SideBar activeElement="editDashboardLink"></SideBar>
        <div class="container">
            <div class="row row-cols-auto justify-content-around" ref="infoBox">
                <template v-for="item in widgetArray" :key="item.id">
                    <div class="col posRel">
                        <div class="del" @click="delWidget(item)">
                            <svg xmlns="http://www.w3.org/2000/svg" width="35" height="35" fill="#D92B04"
                                class="bi bi-dash-circle-fill" viewBox="0 0 16 16">
                                <path
                                    d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zM4.5 7.5a.5.5 0 0 0 0 1h7a.5.5 0 0 0 0-1h-7z" />
                            </svg>
                        </div>
                        <genericWidget :widgetInfo="item"></genericWidget>
                    </div>
                </template>
                <div class="col">
                    <div class="topOfAdd">
                        <router-link to="/addWidget" class="nav-link link-dark">
                            <div class="newWidget">
                                <h4>New widget</h4>
                                <svg xmlns="http://www.w3.org/2000/svg" width="70" height="70" fill="currentColor"
                                    class="bi bi-plus-square" viewBox="0 0 16 16">
                                    <path
                                        d="M14 1a1 1 0 0 1 1 1v12a1 1 0 0 1-1 1H2a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1h12zM2 0a2 2 0 0 0-2 2v12a2 2 0 0 0 2 2h12a2 2 0 0 0 2-2V2a2 2 0 0 0-2-2H2z" />
                                    <path
                                        d="M8 4a.5.5 0 0 1 .5.5v3h3a.5.5 0 0 1 0 1h-3v3a.5.5 0 0 1-1 0v-3h-3a.5.5 0 0 1 0-1h3v-3A.5.5 0 0 1 8 4z" />
                                </svg>
                            </div>
                        </router-link>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import SideBar from "@/components/SideBar/SideBar.vue";
import genericWidget from "@/components/dashboard/widgets/genericWidget.vue";
import axios from "axios";

export default {
    name: "EditDashboard",
    data() {
        return {
            widgetArray: [],
            userId: null,

            componentKey: 0,
        };
    },
    components: {
        SideBar,
        genericWidget
    },
    beforeMount() {
        axios({
            method: "post",
            // url: "http://localhost:8080/api/user/finduser",
            url: "https://www.emuix.com/api/user/finduser",
            data: {
                email: "nat@gmail.com"
            }
        },
        )
            .then((response) => {
                console.log("Then from res 200 : ", response.data);
                this.widgetArray = response.data.data;
                this.userId = response.data._id;
            })
            .catch((response) => {
                console.log("Then from res error : ", response.response.data.message);
            });
    },
    methods: {
        delWidget(widgetInfo) {
            axios({
                method: "post",
                // url: "http://localhost:8080/api/user/delWidget",
                url: "https://www.emuix.com/api/user/delWidget",
                data: {
                    userId: this.userId,
                    deleteId: widgetInfo._id
                }
            },
            )
                .then((response) => {
                    console.log("Then from res 200 : ", response.data);
                    this.widgetArray = response.data.data;
                })
                .catch((response) => {
                    console.log("Then from res error : ", response.response.data.message);
                });
            setTimeout(() => {
                axios({
                    method: "post",
                    // url: "http://localhost:8080/api/user/finduser",
                    url: "https://www.emuix.com/api/user/finduser",
                    data: {
                        email: "nat@gmail.com"
                    }
                },
                )
                    .then((response) => {
                        console.log("Then from res 200 : ", response.data);
                        this.widgetArray = response.data.data;
                        this.userId = response.data._id;
                    })
                    .catch((response) => {
                        console.log("Then from res error : ", response.response.data.message);
                    });
            }, 100);
        },

    },
};
</script>

<style scoped>
.posRel {
    position: relative;
}

.topOfAdd {
    padding-top: 20px;
}

.del {
    position: absolute;
    top: 11px;
    right: -3px;
    /* color: red; */
    z-index: 10;
}

.newWidget {
    border-radius: 12px;
    height: 200px;
    width: 200px;
    background-color: #145374;
    display: table-cell;
    vertical-align: middle;
    color: #ffffff;
}
</style>