import axios from "axios"

let reponseToReturn = null;
export const  weatherUpdate = async (town) => {
    const urlTown = `https://api.openweathermap.org/data/2.5/weather?q=${town}&appid=60e5717b58f0eac31b0b6478cffd766e&units=metric&lang=en`
    await axios({
        method: "get",
        url: urlTown,
    })
        .then((response) => {
            console.log("res 200 from js file : ", response.data);
            reponseToReturn = response.data;
        })
        .catch((response) => {
            console.log("res error from js file: ", response.response.data.message);
        });
        return reponseToReturn;
}